<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入交易订单"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.orderId">
            </el-input>
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入广告编号"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.advertisingID">
            </el-input>
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入买家信息"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.buyerUser">
            </el-input>
            <el-input class="filter-item" style="width: 150px;" placeholder="请输入卖家信息"
                      @keyup.native.enter="getList"
                      v-model="matchQuery.sellerUser">
            </el-input>
            <span style="margin-left: 20px;margin-right: 10px">状态：</span>
            <el-select clearable style="width: 150px" class="filter-item"
                       v-model="matchQuery.coinID">
                <el-option v-for="typeStatus in statusOptions" :key="typeStatus.key"
                           :label="typeStatus.status_name"
                           :value="typeStatus.key">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="typeQuery" size="small">查询</el-button>
            <el-button type="primary" @click="handleDialogShow('add')" v-if="permission.add" size="small">新增</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="expand">
                <template scope="props">
                    <el-form label-position="center" inline class="demo-table-expand">
                        <el-form-item label="买方信息"></el-form-item>
                        <el-form-item label="用户UID">
                            <span>{{ props.row.buyerUser }}</span>
                        </el-form-item>
                        <el-form-item label="昵称">
                            <span>{{ props.row.buyeralaisName }}</span>
                        </el-form-item>
                        <el-form-item label="真实姓名">
                            <span>{{ props.row.buyerrealname }}</span>
                        </el-form-item>
                        <el-form-item label="电话号码">
                            <span>{{ props.row.buyerphone }}</span>
                        </el-form-item>
                        <el-form-item label="邮箱地址">
                            <span>{{ props.row.buyeremail }}</span>
                        </el-form-item>
                        <el-form-item label="类型">
                            <span>{{ props.row.buyerType }}</span>
                        </el-form-item>
                        <el-form-item label="申述号码">
                            <span>{{ props.row.buyerNum }}</span>
                        </el-form-item>
                    </el-form>
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="卖方信息"></el-form-item>
                        <el-form-item label="用户UID">
                            <span>{{ props.row.sellerUser }}</span>
                        </el-form-item>
                        <el-form-item label="昵称">
                            <span>{{ props.row.sellerrealname }}</span>
                        </el-form-item>
                        <el-form-item label="真实姓名">
                            <span>{{ props.row.selleralaisName }}</span>
                        </el-form-item>
                        <el-form-item label="电话号码">
                            <span>{{ props.row.sellerphone }}</span>
                        </el-form-item>
                        <el-form-item label="邮箱地址">
                            <span>{{ props.row.selleremail }}</span>
                        </el-form-item>
                        <el-form-item label="类型">
                            <span>{{ props.row.sellerType }}</span>
                        </el-form-item>
                        <el-form-item label="申述号码">
                            <span>{{ props.row.sellerNum }}</span>
                        </el-form-item>
                    </el-form>
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="交易明细"></el-form-item>
                        <el-form-item label="创建时间">
                            <span>{{ props.row.createTime }}</span>
                        </el-form-item>
                        <el-form-item label="付款时间">
                            <span>{{ props.row.paymentTime }}</span>
                        </el-form-item>
                        <el-form-item label="释放时间">
                            <span>{{ props.row.accomplishTime }}</span>
                        </el-form-item>
                        <el-form-item label="取消时间">
                            <span>{{ props.row.cancelTime }}</span>
                        </el-form-item>
                        <el-form-item label="申述时间">
                            <span>{{ props.row.allegeTime }}</span>
                        </el-form-item>
                        <el-form-item label="申述方UID">
                            <span>{{ props.row.allegeUser }}</span>
                        </el-form-item>
                        <el-form-item label="处理结果">
                            <span>{{ props.row.result }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="170">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 60px">
                            <el-button
                                    type="text" size="small"
                                    @click="passTrade(scope.row.id,scope.row.advertisingID,true)">释放资产
                            </el-button>
                        </li>
                        <li style="float:left;width: 60px">
                            <el-button
                                    type="text" size="small"
                                    @click="passTrade(scope.row.id,scope.row.advertisingID,false)">关闭订单
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="matchQuery.page"
                           :page-sizes="[10,20,30,50]" :page-size="matchQuery.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
        <el-dialog title="收货地址" :visible.sync="dialogFormVisible">
            <el-form :model="alterTradeOrder">
                <el-form-item label="原因">
                    <el-input v-model="alterTradeOrder.remark" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="passorcancel(state)">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>


<script>

    import fetch from '@/utils/fetch';
    import DCTP from '@/utils/dctp';
    import {getTradeOrderList, updateTradeCancel, updateTradePass} from '@/api/otcdealorder';
    import {virtualCoinAndGoods} from '@/api/virtual';

    const statusOptions = [
        {key: 0, status_name: '全部状态'},
        {key: 1, status_name: '等待支付'},
        {key: 2, status_name: '已支付,未放行'},
        {key: 3, status_name: '已完成'},
        {key: 4, status_name: '已取消'},
        {key: 5, status_name: '申诉中'},
    ];

    export default {
        data() {
            return {
                total: null,
                itemWidth: '160px',
                state: '',
                dialogVisible: false,
                feesDialogVisible: false,
                tableData: [],
                cionlist: null,
                dialogFormVisible: false,
                matchQuery: {
                    orderId: '',
                    advertisingID: '',
                    buyerUser: '',
                    sellerUser: '',
                    state: 5,
                    coinID: '',
                    page: 1,
                    pageSize: 10
                },
                alterTradeOrder: {
                    id: '', remark: '', advertId: ''
                },
                statusOptions,
                action: '',
                tableHeader: [
                    {
                        label: "订单编号",
                        prop: "id",
                        width: 180
                    }, {
                        label: "广告",
                        prop: "advertisingID",
                        width: 160,

                    }, {
                        label: "交易类型",
                        prop: "type",
                        width: 120,
                        formatter(row, column, value) {
                            return value === 1 ? '买入' : (value === 2 ? '卖出' : '');
                        }
                    }, {
                        label: "状态",
                        prop: "state",
                        width: 120,
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statusOptions, value, 'key');
                            return item ? item.status_name : '';
                        }
                    }, {
                        label: "购买方",
                        prop: "buyerUser",
                    }, {
                        label: "出售方",
                        prop: "sellerUser",
                    }, {
                        label: "资产名称",
                        prop: "enname",

                    }, {
                        label: "单价",
                        prop: "price",
                    }, {
                        label: "数量",
                        prop: "num",
                    },{
                        label: "手续费",
                        prop: "fees",
                    }, {
                        label: "总价",
                        prop: "total",
                    }, {
                        label: "总价",
                        prop: "total",
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ],
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },

        methods: {
            typeQuery() {
                this.getlist()
            },
            getlist() {
                getTradeOrderList(this.matchQuery).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                        this.listLoading = false;
                    } else {
                        this.tableData = response.data.list;
                        this.total = response.data.total;
                        this.listLoading = false;
                    }
                })
            },
            //翻页的按钮
            userPage(val) {
                this.matchQuery.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.matchQuery.pageSize = val;
                this.getList();
            },
            getcoinlist() {
                let _this = this;
                virtualCoinAndGoods([1], 2, 1).then(response => {
                    if (response.result) {
                        _this.cionlist = response.data;
                    }
                })
            },
            passTrade(id, advertId, state) {
                this.dialogFormVisible = true;
                this.alterTradeOrder.id = id;
                this.alterTradeOrder.advertId = advertId;
                this.state = state;
            },
            passorcancel(state) {
                if (state) {
                    updateTradePass(this.alterTradeOrder).then(response => {
                        if (response.result) {
                            this.msg = response.msg;
                            this.$message({
                                type: 'warning',
                                message: this.msg
                            });
                            this.dialogFormVisible = false;
                            this.getlist();
                        } else {
                            this.msg = response.msg;
                            this.$message({
                                type: 'warning',
                                message: this.msg
                            });
                        }
                    })
                } else {
                    updateTradeCancel(this.alterTradeOrder).then(response => {
                        if (response.result) {
                            this.msg = response.msg;
                            this.$message({
                                type: 'warning',
                                message: this.msg
                            });
                            this.dialogFormVisible = false;
                            this.getlist();
                        } else {
                            this.msg = response.msg;
                            this.$message({
                                type: 'warning',
                                message: this.msg
                            });
                        }
                    })
                }

            }


        },
        created() {
            this.getlist();
            this.getcoinlist();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }

    .input-fee {
        width: 90px;
    }
</style>


